* {
  margin: 0;
  padding: 0;
}

.Login {
  height: 100%;
  width: 100%;
  background: linear-gradient(to bottom left, red, white 50%, black);
  background-position: center;
  background-size: cover;
  position: absolute;
}

.login-form {
  width: 380px;
  height: 480px;
  position: relative;
  margin: 6% auto;
  background: #fff;
  padding: 5px;
  overflow: hidden;
}

.button_box {
  width: 220px;
  margin: 35px auto;
  position: relative;
  box-shadow: 0 0 20px 9px #ff000080;
  border-radius: 30px;
}

.toggle-btn {
  padding: 10px 30px;
  cursor: pointer;
  background: transparent;
  border: 0;
  outline: none;
  position: relative;
}

#btn {
  top: 0;
  left: 0;
  position: absolute;
  width: 110px;
  height: 100%;
  background: linear-gradient(to right, red, white);
  border-radius: 30px;
  transition: 0.5s;
}

.input-group {
  top: 180px;
  position: absolute;
  width: 280px;
  transition: .5s;
}

.input-field {
  width: 100%;
  padding: 10px 0;
  margin: 5px 0;
  border: none;
  border-bottom: 1px solid;
  outline: none;
  background: transparent;
}

.submit-btn {
  width: 85%;
  padding: 10px 30px;
  cursor: pointer;
  display: block;
  margin: auto;
  background: linear-gradient(to right, red, white);
  border: 0;
  outline: none;
  border-radius: 30px;
}

.checkbox {
  margin: 30px 2px 30px 0;
}

span {
  bottom: 62px;
  position: absolute;
}

#login {
  left: 50px;
}

#register {
  left: 450px;
}